.navbar
{
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-right: 10px;
}

.logo {
    display: flex;
    /* background-color: lightblue; */
    width: 150px;
    justify-content: left;
    align-items: center;
    margin-left: 10px;
}
.logo img{
    margin-left: 0px;
    width: 100px;
}
.menuIcon {
    width: 50px;
}


.tag {
    /* background-color: lightgreen; */
    flex: 1;
    margin-left: 10px;
    margin-right: 10px;
    max-width: 500px;

    display:flex;
}
.flex-button {
    flex: 1;
}

.login {
    /* background-color: lightblue; */
    width: 250px;

    display: flex;
    justify-content:right;
}

.nav-link {
    background-color: lightblue;
    margin-left: 10px;
}
.nav-link:hover {
    background-color: lightgreen;
    margin-left: 10px;
}

.nav-link:active {
    background-color: rgb(255, 18, 18);
    color: black;
    margin-left: 10px;
}

.nav-active {
    background-color: rgb(255, 18, 18);
    color: black;
    margin-left: 10px;
}

@media screen and (max-width: 768px) {
    .logo img {
        display: none;
    }
}