.popupModule {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;

    background-color: rgba(49,49,49,0.8);
}

.content {
    display: flex;
    flex-direction: column;
        
    /* width: 500px; */
    /* height: 500px; */
  
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

    position: absolute;
    top: 40%;
    left: 50%;


    transform: translate(-50%, -50%);
}

.titleCloseBtn {
    display: flex;
    justify-content: space-between;
    /* cursor: move; */

    /* margin-left: 10px; */
    border-bottom: 4px solid  #009FCC;
}

.titleCloseBtn button {
    background-color: transparent;
    border: none;
    font-size: 20px;
    cursor: pointer;
}

form .input {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
    margin-left: 10px;
    margin-right: 10px;
}
.inputGroup {
    display: flex;
    flex-direction: column;
}

form .input input, select{
   font-size: 20px;
    background-color: lightblue;
}

form button {
    display: flex;
    justify-content: center;
    align-items: center;
}

.submitBtn {
    display: flex;
    justify-content: center;
}
.submitBtn button {
    background-color: #00B900;
    color: white;
    width: 100px;
    height: 45px;
    margin: 10px;
    border: none;

    border-radius: 5px;
    font-size: 20px;
    cursor: pointer;
}
.submitBtn button:hover {
    background-color: #009300;
}

@media screen and (max-width: 768px) {
    form .input {
        grid-template-columns: repeat(1, 1fr);
    }
}