.Sidebar{
    background-color: #2F4056;
    width:200px;
    /* bottom: 0px; */
    text-decoration: none;
    transition: width 0.6s;
}

.Sidebar.mini{
    width:60px;
}

.items {
    display: flex; 
    color: #C2C6CC;
    justify-content: space-between;
    margin-bottom: 5px;
    text-decoration: none;
    align-items: center;
    transition: color 0.5s;
}

.itemLink {
    display: flex;
    margin-left: 10px;
    align-items: center;
}


.itemLink li {
    list-style: none;
    white-space: nowrap;
    overflow: hidden;
}

.items:hover  {
    /* background-color: #489E94; */
    color: white;
    cursor: pointer;
    /* border-radius: 10px; */
}

.dropdown {
    font-size: 20px;
    transition: transform 0.3s;
}
.dropdown.active {
    transform: rotate(180deg);
}

.subItems {
    display: flex;
    background-color: #222D3B;
    color: #C2C6CC;
    text-decoration: none;
    width:auto;
    padding-left: 20px;
    margin-right: 5px;
    height: 0px;
    overflow: hidden;
    transition: color 0.5s;
}
.subItems:hover  {
    cursor: pointer;
    /* border-radius: 10px; */
    color: white;
}
.subItems.active {
    height: auto;
}

.mini li{
    display: none;
}

.items.current {
    background-color: #1aa094;
}

.subItems.current {
    background-color: #1aa094;
}


@media screen and (max-width: 768px) {
    .Sidebar li {
        /* display: none; */
    }
}