.searchRoot {
    background-color: white;
    margin-top: 20px;
    margin-bottom: 10px;
}

.searchBlock {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.searchForm {
    /* background-color: lightgreen; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-width: 600px;
    gap: 5px;
    /* margin-left: 10px; */
    margin-right: 10px;
}

.searchForm div {
    display:grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    border: 1px solid;
}

.searchForm input, select{
    font-size: 20px;
    width: 230px;
    height: 38px;
    border: none;
}
.searchForm select  {
    background: white;
}
.searchForm label {
    width: 80px;
    height: 30px;
    padding-top: 10px;
    text-align: center;
    border-right: 1px solid;
}

.buttonGroup {
    margin-left: 0px;
}
.searchRoot button {
    margin-top: 10px;
    margin-right: 5px;
    margin-left: 5px;
    font-size: 20px;
    text-align: center;
    cursor:pointer;
}

.searchRoot button svg{
    font-size: 15px;
}


@media screen and (min-width: 1100px) {
    .searchBlock {
        flex-direction: row;
        align-items: flex-end;
        }
    .buttonGroup {
        margin-left: 30px;
    }
}

@media screen and (max-width: 768px) {
    .searchForm {
        grid-template-columns: repeat(1, 1fr);
    }

    .searchRoot{}
}

